html, body {
  margin: 0;
  padding: 0;
  font-family: 'Rubik', sans-serif;
  background-color: #EEF0F4;
  color: #432000;
  user-select: none;
}

.App {
  display: flex;
  flex-direction: column;
  max-width: 320px;
  margin: 30px auto;
}

.meal-list {
  display: grid;
  grid-template-columns: 50% 50%;
}

.add-button {
  /* display: flex; */
  /* flex-direction: column; */
  border: none;
  /* background-color: rgb(39, 104, 104); */
  margin: 5px auto;
  border-radius: 8px;
  width: 90%;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);
  min-height: 200px;
  background-color: #FFFDF8;
  align-items: center;
  font-size: 30px;
  /* justify-content: space-between; */
  /* padding: 10px; */
}

.add-button:hover {
  cursor: pointer;
}

.navbar-group {
  position: fixed;
  display: flex;
  justify-content: space-between;
  top: 0px;
  /* max-width: 320px; */
  width: 100%;
  background-color: #132d42; /* Purple */
  height: 47px;
}

.navbar-search {
display: flex;
}

.navbar-group .button {
  background-color: inherit; /* Purple */
  border: none;
  color: white;
  padding: 15px 15px;
  height: 100%;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  text-align: left;
}

.navbar-group .button:hover {
  background-color: inherit; /* Purple */
}